 
.navbar{
    display: flex;
    justify-content: end;
    height: 70px;
    width: 100vw;
    padding-right: 40px;
    /* border: 1px solid white; */
  }
  
  .navbar-list{
    display: flex;
    justify-content: space-around;
    width: 400px;
    overflow: hidden;
    /* border: 1px solid white; */
  } 
  
  .navbar-list > div{
    margin: auto 0;
    cursor: pointer;
    overflow: hidden;
  }
  
  .navbar-list_name{
    color: white;
    font-size: 17px;
  }
  
  
  .navbar-list_avatar{
    width: 38px;
    height: 38px;
    border-radius: 4px;
    background: url('./icons/avatar.png') center/100% 100% no-repeat;
  }
  
  
  .navbar-list_kyu{
    font-size: 16px;
    color: rgb(236, 182, 19);
    border: 2px solid rgb(236, 182, 19);
    padding: 8px 22px;
    background-color: rgb(20,20,20);
    border-radius: 10px;
  }
  
  .navbar-list_rate{
    color: white;
    font-size: 16px;
  }
  

  @media (max-width: 730px){
      /* background: blue; */
    .navbar-list_name{
      font-size: 14px;
    }
    .navbar-list_kyu{
      font-size: 12px;
      padding: 7px 10px;
    }
    .navbar-list_rate{
      font-size: 14px;
    }

  }

  @media (max-width: 486px){
    /* background: blue; */
  .navbar-list_kyu{
    display: none;
  }

  .navbar-list{
    width: 300px;
  }

}


@media (max-width: 400px){
  /* background: blue; */
.navbar-list_rate{
  display: none;
}

.navbar-list{
  width: 200px;
}

}

@media (max-width: 400px){
  /* background: blue; */
.navbar-list_rate{
  display: none;
}

.navbar-list{
